.switch {
  /* кнопка переключателя */
  position: relative;
  display: inline-block; }
  .switch, .switch__label, .switch__slider {
    width: 26px;
    height: 12px;
    cursor: pointer; }
  .switch__check:checked + .switch__label .switch__slider_on {
    width: 26px; }
  .switch__check:checked + .switch__label .switch__slider_off {
    width: 12px; }
  .switch__slider_off:after {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    top: -2px; }
  .switch__slider {
    border-radius: 6px;
    box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.15) inset, 0 0 2px rgba(0, 0, 0, 0.1) inset, 0 4px 2px 1px rgba(0, 0, 0, 0.1) inset, 0 6px 6px 0 rgba(0, 0, 0, 0.07) inset; }
    .switch__slider_on {
      width: 12px;
      background: #41fb19; }
  .switch__check, .switch__label, .switch__slider, .switch__slider_off:after {
    position: absolute; }
  .switch__check {
    opacity: 0; }
  .switch__label {
    top: 0;
    left: 0; }
  .switch__slider {
    top: 0;
    -webkit-transition: width 0.15s linear;
    -moz-transition: width 0.15s linear;
    -o-transition: width 0.15s linear;
    transition: width 0.15s linear; }
    .switch__slider_on {
      left: 0; }
    .switch__slider_off {
      right: 0;
      background: #fff; }
      .switch__slider_off:after {
        content: '';
        left: -2px;
        background: #fff;
        background-image: -webkit-linear-gradient(0deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0.06) 100%);
        background-image: -moz-linear-gradient(0deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0.06) 100%);
        background-image: -o-linear-gradient(0deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0.06) 100%);
        background-image: -ms-linear-gradient(0deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0.06) 100%);
        background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.06) 0%, rgba(255, 255, 255, 0.06) 100%);
        box-shadow: 0 0 1px 1px #fff inset, 0 0 2px 1px rgba(0, 0, 0, 0.3); }

